import React from 'react';
import Profile from './components/Profile/Profile';
import user from './profile.json';
import './style.css';
console.log(user);
const App = () => {
  return (
    <div>
      <Profile
        name={user.name}
        tag={user.tag}
        location={user.location}
        avatar={user.avatar}
        stats={user.stats}
      />
    </div>
  );
};

export default App;
